@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body,
html,
#root {
  height: 100%;
  font-family: "Roboto", sans-serif;
  color: white;
  background: linear-gradient(#050222 0%, #3b083d 30%, #0a0630 100%);
  scroll-behavior: smooth;
}

.App {
  background: rgb(37, 19, 51);
  background: linear-gradient(#050222 0%, #3b083d 30%, #0a0630 100%);
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 25px;
}

.header {
  padding: 20px;

  .nav,
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav {
    display: flex;
    justify-content: space-between;
    padding: 15px 20px;
    border: 1px solid #a8a8a884;
    border-radius: 12px;
    position: relative;

    .hamburger {
      z-index: 2;
    }
  }

  .menu {
    display: flex;
    &.mobile {
      display: none;
      flex-direction: column;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      background-color: #15061e;
      z-index: 1;
      justify-content: flex-start;
      border-radius: 50px;
      border: 1px solid #2d052c;
      box-shadow: 0 0 30px 19px #410d3fa8;
      padding: 40px 0;
      z-index: 1;

      &.open {
        display: flex;
        overflow: hidden;
        .link {
          margin: 20px 0;
        }
      }
    }
    .link {
      margin: 0 10px;

      /* @media (min-width: 600px) {
     
      } */

      a {
        text-decoration: none;
        color: white;
        &.active {
          color: #3b6aa0;
        }
      }
    }
  }

  .toggle-btn {
    background-color: transparent;
    border: 1px solid #aaa;
    border-radius: 99px;
    width: 54px;
    height: 28px;
    transition: background-color 0.1 ease, border-color 0.2s ease;
    cursor: pointer;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.748);
    position: relative;

    span {
      display: none;
      color: white;

      margin: 0 5px;
      &.visible {
        display: block;
      }
      &.left {
        text-align: left;
      }
      &.right {
        text-align: right;
      }
    }

    &.toggled {
      .thumb {
        left: calc(50px - 24px);
      }
    }
    .thumb {
      height: 20px;
      width: 20px;
      background-color: #fff;
      border-radius: 99px;
      transform: translateX(0);
      transition: left 0.15s ease;
      position: absolute;
      top: 50%;
      left: 3px;
      transform: translateY(-50%);
      background-size: cover;
      background-position: center;
      /* &.pl {
        background-image: url("/PL.svg");
      }
      &.eng {
        background-image: url("/GB.svg");
      } */
    }
  }
}

h2.title {
  font-size: 25px;
  text-align: center;
  @media (min-width: 600px) {
    font-size: 28px;
    text-align: left;
  }

  background: -webkit-linear-gradient(#d22b81, #3085d7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  border-bottom: 1px solid #6464648a;

  @media (min-width: 600px) {
    flex-direction: row;
  }

  .image {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-width: 600px) {
      width: 50%;
    }
  }

  .box {
    padding: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: 600px) {
      display: block;
      width: 50%;
      margin: auto;
    }
    h2 {
      font-size: 48px;
      line-height: 1;
      margin-bottom: 10px;
    }
    p {
      font-size: 16px;
      text-align: center;
      @media (min-width: 600px) {
        text-align: left;
      }
    }
    .neon-button {
      margin-top: 10px;
      font-size: 11px;
      font-weight: bold;
      text-transform: uppercase;
      color: white;
      background: linear-gradient(90deg, #72088d, #069696);
      border: none;
      padding: 12px 24px;
      border-radius: 11px;
      cursor: pointer;
      text-decoration: none;
      display: inline-block;
      position: relative;
      transition: 0.3s ease-in-out;
      box-shadow: 2px 4px 5px rgba(80, 5, 151, 0.5),
        0 0 10px rgba(2, 116, 116, 0.5);
      position: relative;
      z-index: 0;
    }

    .neon-button:hover {
      box-shadow: 0 0 12px rgba(255, 0, 255, 0.8),
        0 0 12px rgba(0, 255, 255, 0.8);
    }
  }
}

/* Sekcja services */
.services {
  padding: 40px 20px;
  border-bottom: 1px solid #6464648a;
  .title {
    margin-bottom: 40px;
  }
  .items {
    max-width: 1200px;
    margin: 0 auto;
    .item {
      padding: 20px;
      border: 1px solid #6060609c;
      border-radius: 21px;
      width: 90%;
      background-color: #00000010;
      @media (min-width: 600px) {
        width: 60%;
      }
      @media (min-width: 1200px) {
        max-width: 660px;
      }
      &:nth-child(2) {
        margin-left: auto;
        margin: 25px 0 25px auto;
        position: relative;
        &::before {
          content: "";
          position: absolute;
          width: 1px;
          height: 26px;
          background-color: #6060609c;
          top: -26px;
          left: 50px;
        }
        &::after {
          content: "";
          position: absolute;
          width: 1px;
          height: 26px;
          background-color: #6060609c;
          bottom: -26px;
          left: 50px;
        }
      }
      .head {
        display: flex;
        .title {
          margin: -4px 0 10px 10px;
          font-size: 17px;
          line-height: 1.2;
          color: #a07ce9;
          @media (min-width: 600px) {
            font-size: 20px;
          }
        }
      }
      .body {
        font-size: 13px;
        @media (min-width: 600px) {
          font-size: 16px;
        }
      }
      .img {
        min-width: 25px;
        height: 25px;
        fill: #1d6fb2;
      }
    }
  }
}

.skills {
  padding: 40px 20px;
  border-bottom: 1px solid #6464648a;

  p {
    text-align: center;
    font-size: 22px;
    margin: 30px 0;
  }
  .icons {
    margin: 20px 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 75%;
    margin: 0 auto;

    @media (min-width: 1200px) {
      width: 35%;
    }

    * {
      margin: 10px;
    }
  }
}

.portfolio {
  padding: 40px 20px;
  border-bottom: 1px solid #6464648a;
  .title {
    margin-bottom: 20px;
  }
  .items {
    flex-direction: row;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (min-width: 600px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto;
      gap: 10px;
      max-width: 500px;
      margin-right: auto;
    }

    .item {
      max-width: 200px;
      margin: 0 auto;
      display: block;
      margin-bottom: 20px;
      grid-row: span 1;

      @media (min-width: 600px) {
        width: 250px;
        height: 200px;
      }

      &:nth-child(1) {
        height: auto;
        grid-row: span 2;
      }

      img {
        width: 100%;
      }
      .item-content {
        text-align: center;
        @media (min-width: 600px) {
          text-align: left;
        }
        .title {
          font-size: 22px;
          margin-bottom: 10px;
          color: #3b6aa0;
        }
        .desc {
          margin-bottom: 10px;
        }
      }
    }
  }
}

.contact {
  padding: 40px 20px;
  border-bottom: 1px solid #6464648a;
  display: flex;
  flex-direction: column;

  @media (min-width: 600px) {
    flex-direction: row;
  }
  .left {
    width: 100%;

    .title {
      margin-bottom: 25px;
    }

    .subtitle {
      display: flex;
      text-align: center;
      margin-bottom: 25px;

      @media (min-width: 600px) {
        text-align: left;
      }
      svg {
        margin-right: 5px;
      }
    }

    .box {
      display: flex;
      justify-content: space-between;
      margin-right: 100px;
      flex-direction: column;

      @media (min-width: 900px) {
        flex-direction: row;
      }
    }

    .description {
      margin-bottom: 30px;
      .link {
        color: #9191fe;
      }
    }
    @media (min-width: 600px) {
      width: 50%;
    }
  }

  .right {
    width: 100%;
    @media (min-width: 600px) {
      width: 50%;
    }

    .formTitle {
      margin: 10px 0;
    }

    form {
      display: flex;
      flex-direction: column;
      input,
      textarea {
        background: transparent;
        border: 1px solid #c6c5c55b;
        border-radius: 10px;
        outline: none;
        color: white;

        margin: 5px 0;
        @media (min-width: 600px) {
          margin: 15px 0;
        }
      }
      input {
        height: 30px;
      }
      textarea {
        height: 50px;
      }

      .form-btn {
        width: 100px;
        margin-top: 10px;
        font-size: 11px;
        font-weight: bold;
        text-transform: uppercase;
        color: white;
        background: linear-gradient(90deg, #72088d, #069696);
        border: none;
        padding: 12px 24px;
        border-radius: 11px;
        cursor: pointer;
        text-decoration: none;
        transition: 0.3s ease-in-out;
        box-shadow: 2px 4px 5px rgba(80, 5, 151, 0.5),
          0 0 10px rgba(2, 116, 116, 0.5);
        align-self: flex-end;

        @media (min-width: 600px) {
          align-self: flex-start;
        }

        &:hover {
          box-shadow: 0 0 12px rgba(255, 0, 255, 0.8),
            0 0 12px rgba(0, 255, 255, 0.8);
        }
      }
    }
  }
}

/* Stopka */
.footer {
  color: #909090;
  padding: 15px;
  text-align: center;
  @media (min-width: 600px) {
    text-align: left;
  }
}
